const CAKE_ORDERED = "CAKE_ORDERED";
const CAKE_RESTOCKED = "CAKE_RESTOCKED";

const cakeOrdered = (quantity)=>{
    return{
        type:CAKE_ORDERED,
        payload:quantity
    }
}
const cakeRestocked = (quantity)=>{
return{
    type:CAKE_RESTOCKED,
    payload:quantity
}
}

export {cakeOrdered, cakeRestocked, CAKE_ORDERED, CAKE_RESTOCKED}