import { CAKE_ORDERED, CAKE_RESTOCKED } from "./constants"

const initialState ={
    numOfCakes : 20
}
const reducer = (state=initialState, action)=>{
    switch(action.type){
        case CAKE_ORDERED:
            if(state.numOfCakes > action.payload)
            return{
                ...state,
                numOfCakes: state.numOfCakes - action.payload
            }
            else{
                alert('Not Engouh cakes')
            }
        case CAKE_RESTOCKED:
            return{
                ...state,
                numOfCakes: state.numOfCakes + action.payload
            }
        default:
            return{
                ...state
            }       
    }
}

export default reducer