import React from 'react'
import { cakeOrdered, cakeRestocked } from './redux/constants';
import { useSelector } from 'react-redux';
import { useState } from 'react';
import Navbar from './components/Navbar';

import store from './redux/store';
import { Button } from 'bootstrap';

function App() {
  const cakes = useSelector((state)=>state.numOfCakes)
  const [value, setValue] = useState('')
  return (
   <>
    <Navbar/>
    <h3>Number of cakes Available: {cakes}</h3>
    <div className='row'>
      <div className='col-md-4'>
          <input type="text" class="form-control" aria-label="Small" placeholder='Enter Cake value to order/stock' value={value} 
          onChange={(e)=>setValue(e.target.value)}
          />
      </div>
      <div className='col-md-8'>

      <button className='btn btn-primary' onClick={()=>{
      store.dispatch(cakeOrdered(parseInt(value)))
    }}>Order cake</button>

    <button className='btn btn-primary' style={{marginLeft:'10px'}} onClick={()=>{
      store.dispatch(cakeRestocked(parseInt(value)))
    }}
    >Cake restock</button>
      </div>
     
    </div>
    
   </>
  );
}

export default App;
